import React, {PropTypes} from 'react';

const DataEntryButtonBar = ({isCreate, onCancel, onSave, onSaveNew, onDelete, saving}) => {
    let wrapperClass = 'form-group';
    /*if (error && error.length > 0) {
        wrapperClass += " " + 'has-error';
    }*/
    let deleteButton = null;
    if(!isCreate) {
        deleteButton = <button onClick={onDelete} className="btn btn-default  ">Delete</button>;
    }

    return (
        <div className={wrapperClass}>
                    {deleteButton}
                    <button onClick={onCancel} className="btn btn-default  "><i className="fa fa-floppy-o"></i></button>
                    
                    <button
                        type="submit"
                        disabled={saving}
                        className="btn btn-primary"
                        onClick={onSave}>{saving ? 'Saving...' : 'Save'}</button>
            
                    <button
                        type="submit"
                        disabled={saving}
                        className="btn btn-primary"
                        onClick={onSaveNew}>{saving ? 'Saving...' : 'Save & New'}</button>
        </div>
    );
};
/*<input
                        type="submit"
                        disabled={saving}
                        value={saving ? 'Saving...' : 'Save'}
                        className="btn btn-primary"
                        onClick={onSave}/>
                    
                    <input
                        type="submit"
                        disabled={saving}
                        value={saving ? 'Saving...' : 'Save & New'}
                        className="btn btn-primary"
                        onClick={onSaveNew}/>*/

DataEntryButtonBar.propTypes = {
    isCreate: PropTypes.bool.isRequired,
    onCancel: PropTypes.func.isRequired,
    onSave: PropTypes.func.isRequired,
    onSaveNew: PropTypes.func.isRequired,
    onDelete: PropTypes.func.isRequired,
    saving: PropTypes.bool
};

export default DataEntryButtonBar;



