import React, {PropTypes} from 'react';
import {Link, browserHistory} from 'react-router';
import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';
import ItemtypeList from './ItemtypeList';
import NewItemtypePage from './NewItemtypePage';
import * as actions from '../../../actions/admin/itemtypeActions';
import util from '../../../util/util';

class ItemtypesPage extends React.Component {
    constructor(props, context) {
        super(props, context);
    }
    
    componentWillMount() {
        if (this.props.itemtypes[0].id == '') {
            this.props.actions.loadItemtypes();
        }
    }
    
    render() {
        const itemtypes = this.props.itemtypes;
        console.log(util);
        console.log(util.format.forDisplay.bool.asCheckBlank(true));
        return (
            <div className="col-md-12">
                <div>{this.props.children}</div>
                <div>
                    <table className="table table-sm table-striped table-hover">
                        <thead>
                            <tr><th colSpan="3"><h3>Itemtypes</h3></th></tr>
                            <tr><th>--</th><th>isPicklist</th><th>--</th></tr>
                        </thead>
                        <ItemtypeList itemtypes={itemtypes} />
                    </table>
                </div>
            </div>
        );
    }
}
/*
            <div className="col-md-12">
                <h1>Itemtypes <Link to={'/admin/itemtype/0'} className="btn btn-primary">New Itemtype</Link></h1>
                <div className="col-md-6">
                    <ItemtypeList itemtypes={itemtypes} />
                </div>
                <div className="col-md-6">
                    {this.props.children}
                </div>
            </div>*/
ItemtypesPage.propTypes = {
    itemtypes: PropTypes.array.isRequired,
    children: PropTypes.object
};

function mapStateToProps(state, ownProps) {
    if (state.itemtypes.length > 0) {
        return {
            itemtypes: state.itemtypes
        };
    } else {
        return {
            itemtypes: [util.objectModel.ITEMTYPE]
        };
    }
}

function mapDispatchToProps(dispatch) {
    return {actions: bindActionCreators(actions, dispatch)};
}

export default connect(mapStateToProps, mapDispatchToProps)(ItemtypesPage);








