import React, {PropTypes} from 'react';
import {Link} from 'react-router';
import ItemtypePage from './ItemtypePage';
import util from '../../../util/util';
import FontAwesome from 'react-fontawesome';


const ItemtypeListItem = ({itemtype}) => {
    //console.log(FontAwesome);
    return (
        <tr key={itemtype.id}>
            <td>{itemtype.name}</td>
            <td>{util.format.forDisplay.bool.asCheckX(itemtype.isPicklist)}</td>
            <td>
                <Link to={'/admin/Itemtype/' + itemtype.id} className="btn btn-primary">Edit</Link>
            </td>
            <td>
                <FontAwesome name="rocket" />
                <i className="fa fa-rocket"></i>
            </td>
        </tr>
    );
};
/*<li className="list-group-item" key={itemtype.id}>
            <Link to={'/admin/itemtype/' + itemtype.id}>{itemtype.name}</Link>
            <Link to={'/admin/Itemtype/' + itemtype.id} className="btn btn-primary">Edit</Link>
        </li>*/
ItemtypeListItem.propTypes = {
    itemtype: PropTypes.object.isRequired
};

export default ItemtypeListItem;



